<template>
  <v-container align-content-center>
    <v-layout justify-space-around row wrap>
      <v-flex lg5>
        <div v-for="video in videos" :key="video.id">
          src: {{ video.src }}
          <iframe
            id="player"
            type="text/html"
            width="720"
            height="405"
            :src="video.src"
            frameborder="0"
            allowfullscreen
          ></iframe>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      videos: [],
      bottom: false,
      pageToken: "",
      viewCount: 0
    };
  },
  methods: {
    getVideos() {
      const axios = require("axios");
      const APIKEY = "AIzaSyC9gTmYA4ies0KSkMhx9KQrmQTA7wVVMCM";
      const url = `https://www.googleapis.com/youtube/v3/search?part=snippet&channelId=UC_x5XG1OV2P6uZZ5FSM9Ttw&pageToken=${
        this.pageToken
      }&maxResults=5&order=viewCount&type=video&key=${APIKEY}`;
      return axios.get(url).then(response => {
        if (response.status === 200) {
          this.pageToken = response.data.nextPageToken;
          this.viewCount++;
          return response.data.items.map(item => ({
            id: item.id.videoId,
            src: "https://www.youtube.com/embed/" + item.id.videoId
          }));
        }
      });
    },

    bottomVisible() {
      const scrollY = window.scrollY;
      const visible = document.documentElement.clientHeight;
      const pageHeight = document.documentElement.scrollHeight;
      const bottomOfPage = visible + scrollY >= pageHeight;
      return bottomOfPage || pageHeight < visible;
    },
    addVideos() {
      if (this.bottomVisible()) {
        this.getVideos().then(videos => {
          this.videos.push(...videos);
        });
      }
    }
  },
  watch: {
    bottom(bottom) {
      if (bottom) {
        this.addVideos();
      }
    }
  },
  mounted() {
    this.getVideos().then(videos => {
      this.videos.push(...videos);
    });
  },
  created() {
    window.addEventListener("scroll", () => {
      this.bottom = this.bottomVisible();
    });
    this.addVideos();
  }
};
</script>
